.single-flashcard-container {
  display: flex; 
  justify-content: center;
  align-items: center;
  margin: 20px 0;
  margin-bottom: 30px;
  width: 80vw;
  min-height: 250px;
}

 .single-flashcard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* min-height: 250px; */
  position: relative;
  -webkit-transition: -webkit-transform 0.8s;
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}


.single-flashcard-front, .single-flashcard-back {
  position: absolute;
  flex: 1 1;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;  
}

.single-flashcard-front {
  background-color: black;
  color: white;
  min-height: 250px;
  width: 80vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.single-flashcard-back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  background-color: #bbb;
  min-height: 250px;
  width: 80vw;
  background-color: white;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.card-flip-motion,
.card-flip-motion .single-flashcard-container {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

